<template>
	<div class="toast"  v-show="isShow">
		<div>{{message}}</div>
	</div>
</template>

<script>


export default {
  name: 'Toast',
  components:{
  },
  data(){
	return{
		message:'',
		isShow:false,
		timer:null
	}
  },
  methods:{
	show(message,duration=2000){
		this.isShow=true
		this.message=message
		clearInterval(this.timer)
		
		this.timer=setTimeout(()=>{
			this.isShow=false
			this.message=''
		},duration)
	}
  }
}
</script>


<style>
.toast{
	position:fixed;
	top:50%;
	left:50%;
	color:white;
	transform:translate(-50%,-50%);
	padding:8px 10px;
	background:rgba(0,0,0,.6);
	z-index:99;
	border-radius:2px;
}

</style>
