import {request} from './request'

export function getCustomer() {
  return request({
    //url:'/test/getBanners'
	//url:'/vue/VueProjectData/home/product.php'
	url:'/vue/VueProjectData/manager/customer.php'
  });
}

//获取客户
export function getCustomerList(typeid=0,page=1) {
  return request({
	url:'/vue/VueProjectData/manager/customer.php',
    params:{
		typeid,
		page
    }
  });
}

//获取详情客户
export function getCustomerDetail(cid,page=1,documenttype='allDocuments') {
  return request({
	url:'/vue/VueProjectData/manager/customerdetail.php',
    params:{
		cid,
		page,
		documenttype
    }
  });
}

//添加客户
export function SeedAddCustomerData(obj,page=0) {
  return request({
	url:'/vue/VueProjectData/manager/addcustomer.php',
    params:{
      obj,
      page
    }
  });
}